/* Layouts */

.navbar {
  &.fixed-top {
    + .page-body-wrapper {
      padding-top: $navbar-height;
    }
  }
}


// Sidebar Mini
.sidebar-mini {
  @media (min-width: 992px) {
    .navbar {
      .navbar-brand-wrapper {
        width: $sidebar-width-mini;
      }
      .navbar-menu-wrapper {
        width: calc(100% - #{$sidebar-width-mini});
      }
    }
    .sidebar {
      width: $sidebar-width-mini;
      .nav {
        .nav-item {
          .nav-link {
            display: block;
            text-align: center;
            position: relative;
            .menu-title {
              display: inline-block;
            }
            .badge {
              margin-left: 5px;
              display: none;
            }
            .menu-icon {
              margin-left: auto;
              margin-right: auto;
            }
            i {
              &.menu-arrow {
                display: inline-block;
                margin-left: 5px;
                position: absolute;
                bottom: 28px;
                right: 10px;
              }
            }
          }
          &.nav-category {
            display: none;
          }
        }
      }
    }

    .content-wrapper,
    .footer {
      margin-left: $sidebar-width-mini;
      width: calc(100% - #{$sidebar-width-mini});
      @at-root #{selector-append(".rtl", &)} {
        margin-left: 0;
        margin-right: $sidebar-width-mini;
      }
    }
    &:not(.sidebar-icon-only) {
      //Apply these styles only when sidebar-mini is not collapsed to icon-only mode
      .sidebar {
        .nav {
          .nav-item{
            &.account-dropdown{
              p{
                margin-left: 0 !important;
              }
              .sub-menu{
                padding: 0 0 1rem 0;
                .nav-item{
                  .nav-link{
                    @include display-flex;
                    @include justify-content(start);
                    padding: 0.3rem 0;
                    .menu-icon{
                      margin-left: 0;
                      margin-right: 1.25rem;
                    }
                  }
                }
              }
            }
          }
          &:not(.sub-menu) {
            > .nav-item {
              border-bottom: 1px solid rgba($sidebar-dark-menu-color, .2);
              .nav-link {
                height: auto;
                padding: $sidebar-mini-menu-padding;
                i {
                  &.menu-icon {
                    margin-bottom: .5rem;
                  }
                }
              }
            }
          }
          &.sub-menu {
            padding: 0;
            .nav-item {
              .nav-link {
                padding: 1rem 0;
                text-align: center;
                &:before {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
}

// Sidebar Icon Only
.sidebar-icon-only {
  @media (min-width: 992px) {
    .navbar {
      .navbar-brand-wrapper {
        width: $sidebar-width-icon;
        .brand-logo {
          display: none;
        }
        .brand-logo-mini {
          display: inline-block;
        }
      }
      .navbar-menu-wrapper {
        width: calc(100% - #{$sidebar-width-icon});
      }
    }
    .sidebar {
      width: $sidebar-width-icon;
      .nav {
        overflow: visible;
        .nav-item {
          position: relative;
          &.account-dropdown{
            display: none;
          }
          .nav-link {
            display: block;
            text-align: center;
            .menu-title,
            .badge {
              display: none;
            }
            i {
              &.menu-icon {
                margin-right: 0;
              }
              &.menu-arrow {
                display: none;
              }
            }
          }
          &.nav-category {
            display: none;
          }
          .collapse {
            display: none;
          }
          &.hover-open {
              .nav-link {
                .menu-title {
                  @include display-flex;
                  @include align-items(center);
                  background: $sidebar-dark-menu-active-bg;
                  padding: 0.5rem 2rem;
                  left: $sidebar-width-icon;
                  position: absolute;
                  text-align: left;
                  top: 0;
                  width: $sidebar-width-lg;
                  z-index: 1;
                  height: $nav-link-height;
                  line-height: 1.8;
                  @at-root #{selector-append(".rtl", &)} {
                      left: auto;
                      right: $sidebar-width-icon;
                      text-align: right;
                  }
                  &:after {
                    display: none;
                  }
                }
              }
              .collapse,
              .collapsing {
                display: block;
                padding: .5rem 0;
                background: $sidebar-dark-bg;
                position: absolute;
                top: $nav-link-height;
                left: calc(15px + #{$sidebar-width-icon});
                width: calc(#{$sidebar-width-lg - 15px});
                @at-root #{selector-append(".rtl", &)} {
                    left: auto;
                    right: calc(15px + #{$sidebar-width-icon});
                }
                -webkit-box-shadow: 0 1px 15px 1px rgba(0,0,0,1);
                -moz-box-shadow: 0 1px 15px 1px rgba(0,0,0,1);
                box-shadow: 0 1px 15px 1px rgba(0,0,0,1);
              }
            }
        }
        &.sub-menu {
          padding: $sidebar-icon-only-submenu-padding;
          .nav-item {
            .nav-link {
              text-align: left;
              @at-root #{selector-append(".rtl", &)} {
                text-align: right;
              }
            }
          }
        }
      }
    }

    .content-wrapper,
    .footer {
      margin-left: $sidebar-width-icon;
      width: calc(100% - #{$sidebar-width-icon});
      @at-root #{selector-append(".rtl", &)} {
        margin-left: 0;
        margin-right: $sidebar-width-icon;
      }
    }
  }
}

// Hidden Sidebar
.sidebar-hidden {
  @media (min-width: 992px) {
    .sidebar {
      transition: width $action-transition-duration $action-transition-timing-function;
      -webkit-transition: width $action-transition-duration $action-transition-timing-function;
      -moz-transition: width $action-transition-duration $action-transition-timing-function;
      -ms-transition: width $action-transition-duration $action-transition-timing-function;
      width: 0;
    }

    .content-wrapper,
    .footer {
      margin-left: 0;
      width: 100%;
      @at-root #{selector-append(".rtl", &)} {
        margin-right: 0;
      }
    }
  }
}

// Absolute sidebar with overlay to content
.sidebar-absolute {
  @media (min-width: 992px) {
    &:not(.sidebar-hidden) {
      .sidebar {
        -webkit-box-shadow: 0px 0px 3px 1px $black;  /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
        -moz-box-shadow:    0px 0px 3px 1px $black;  /* Firefox 3.5 - 3.6 */
        box-shadow:         0px 0px 3px 1px $black;  /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
      }
    }

    .content-wrapper,
    .footer {
      margin-left: 0;
      width: 100%;
      @at-root #{selector-append(".rtl", &)} {
        margin-right: 0;
      }
    }
  }
}

//Horizontal Menu
.horizontal-menu {
  @media (min-width: 992px) {
    .navbar-toggler {
      &[data-toggle="minimize"],
      &[data-toggle="toggleSidebarDisplay"] {
        display: none;
      }
    }

    .content-wrapper,
    .footer {
      margin-left: 0;
      width: 100%;
      @at-root #{selector-append(".rtl", &)} {
        margin-right: 0;
      }
    }

    .content-wrapper {
      padding-top: 6rem;
    }
    .sidebar{
      width: 100%;
      min-height: auto;
      border-top: 1px solid rgba($sidebar-dark-menu-color, .3);
      border-bottom: 1px solid $border-color;
      .nav {
        flex-direction: row;
        margin-bottom: 0;
        margin-top: 0;
        overflow: visible;
        padding: 0;
        .nav-item {
          position: relative;
          padding: $horizontal-menu-item-padding;
          .nav-link {
            height: auto;
            border-bottom: none;
            @include display-flex;
            .menu-icon {
              font-size: $horizontal-menu-icon-font-size;
              line-height: 1;
              margin-left: 0;
              text-align: center;
            }
            .menu-title {
              font-size: $horizontal-menu-font-size;
              line-height: 1;
              display: block;
              font-family: $type1-bold;
            }
          }
          &.hover-open {
            .collapse,
            .collapsing {
              display: block;
              background: #020202;
              position: absolute;
              top: 60px;
              left: auto;
              right: auto;
              padding: 1.5rem 1rem;
              -webkit-box-shadow: 0 1px 15px 1px rgba(0,0,0,0.20);
              -moz-box-shadow: 0 1px 15px 1px rgba(0,0,0,0.20);
              box-shadow: 0 1px 15px 1px rgba(0,0,0,0.20);
              @include border-radius(5px);
              border: none;
              @at-root #{selector-append(".rtl", &)} {
                  left: auto;
                  right: 0;
              }
            }
            &.mega-menu {
              position: static;
              .collapse,
              .collapsing {
                width: auto;
                .mega-menu-section {
                  width: auto;
                  margin-left: 15px;
                  margin-right: 15px;
                  float: left;
                  .mega-menu-title {
                    .nav-link {
                      color: $white;
                      font-size: $default-font-size;
                      border-bottom: 1px solid $border-color;
                      line-height: 1;
                    }
                  }
                  .mega-menu-title,
                  .nav-item {
                    .nav-link {
                      padding-left: 0;
                      padding-right: 0;
                    }
                  }
                }
              }
            }
          }
        }
        &.sub-menu {
          padding: 0;
          .nav-item {
            padding: 0;
            .nav-link {
              color: $horizontal-menu-submenu-color;
              padding: $horizontal-menu-submenu-item-padding;
              font-size: $default-font-size;
              &:before {
                display: none;
              }
            }
            &:last-child {
              .nav-link {
                border-bottom: 0;
              }
            }
          }
        }
      }
      >.nav {
        >.nav-item {
          >.nav-link {
            padding: .55rem 1.5rem;
          }
          &:last-child {
            .nav-link {
              border-right: none;
            }
          }
        }
      }
    }
  }
}

//Fixed sidebar
.sidebar-fixed {
  .sidebar {
    @media(min-width: 992px) {
      position: fixed;
    }
    .nav {
      max-height: calc(100vh - #{$navbar-height});
      overflow: auto;
      position: relative;
      &.sub-menu {
        max-height: none;
      }
    }
  }
}

//Boxed layout
.boxed-layout {
  @media (min-width: 992px) {
    .container-scroller {
      background: #fff;
      padding: 0 (100% - $boxed-container-width) / 2;
    }
    .navbar {
      &.fixed-top {
        margin: auto;
        width: $boxed-container-width;
      }
    }
    &.horizontal-menu {
      .sidebar {
        margin: auto;
        width: $boxed-container-width;
      }
      &.horizontal-menu-top {
        .sidebar {
          width: calc(#{$boxed-container-width} - #{$sidebar-width-lg});
        }
      }
    }
  }
}

//RTL layout
.rtl {
  direction: rtl;
  text-align: right;
  .content-wrapper {
    margin-left: 0;
    margin-right: $sidebar-width-lg;
    @media (max-width: 991px) {
      margin-right: 0;
    }
  }
  .sidebar {
    .nav {
      padding-right: 0;
      .nav-item {
        .nav-link {
          .menu-icon {
            margin-right: 0;
            margin-left: .5em;
          }
          i {
            &.menu-arrow {
              margin-left: 0;
              margin-right: auto;
            }
          }
          .badge {
            margin-left: 0;
            margin-right: auto;
          }
        }
        &.account-dropdown{
          p{
            @extend .ml-0;
            @extend .mr-3;
          }
          .nav-link{
            padding-left: 0;
            padding-right: $account-dropdown-padding;
          }
        }
      }
      &.sub-menu {
        padding: $rtl-sidebar-submenu-padding;
      }
    }
  }
  .footer {
    margin-left: 0;
    margin-right: $sidebar-width-lg;
    @media (max-width: 991px) {
      margin-right: 0;
    }
  }

  .settings-panel .tab-content .tab-pane .scroll-wrapper,
  ul.chats,
  .product-chart-wrapper,
  .table-responsive,
  .sidebar-fixed .nav {
    &::-webkit-scrollbar {
        width: .5em;
    }
    &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    }
    &::-webkit-scrollbar-thumb {
      background-color: darkgrey;
      outline: 1px solid slategrey;
    }
  }
}
